*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

/* Firefox: */
@-moz-document url-prefix() {
  select {
    text-indent: -2px; } }

/* Edge: */
@supports (-ms-ime-align: auto) {
  select {
    width: calc(100% + 4px);
    margin-left: -4px; } }

/* IE11: */
@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  select {
    width: calc(100% + 4px);
    margin-left: -3px; } }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff"); }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */ }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.text {
  font-size: 13px;
  line-height: 18px;
  color: #000000;
  margin: 0; }

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none; }

.row {
  display: flex;
  flex-direction: row; }

.column {
  display: flex;
  flex-direction: column; }

.margin-reset {
  margin: 0; }

.hover-opacity {
  transition: opacity 0.3s ease; }
  .hover-opacity:hover, .hover-opacity:focus {
    opacity: 0.7; }
  .hover-opacity:active {
    opacity: 0.5; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }

.header .container {
  display: flex; }

.header__user-nav {
  display: flex;
  align-items: center;
  margin-left: auto; }

.header__user {
  display: flex;
  align-items: center; }
  .header__user img {
    width: 32px;
    height: 32px;
    object-fit: contain;
    margin-right: 0.75rem; }

.header__logout {
  width: 20px;
  height: 20px;
  margin-left: 0.75rem;
  background-image: url("../img/bg/icon-exit.png");
  background-repeat: no-repeat;
  background-size: 15px 15px;
  background-position: center;
  font-size: 0; }

.breadcrumbs {
  background-color: #f3f2f2; }
  .breadcrumbs__list {
    display: flex;
    align-items: center;
    min-height: 48px;
    padding-left: 3.5rem; }
    .breadcrumbs__list li {
      margin-right: 1rem;
      position: relative;
      color: #707478; }
      .breadcrumbs__list li:not(:last-child)::after {
        content: "";
        display: block;
        width: 5px;
        height: 9px;
        background-image: url("../img/sprite/icon-tick.svg");
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        right: -0.7rem;
        top: 0.3rem; }
  .breadcrumbs a[href] {
    color: #058dc7; }

.employees {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  flex-grow: 1;
  max-width: 341px; }
  .employees__search {
    display: flex;
    min-height: 22px;
    background-color: #f9f8f6; }
    .employees__search label {
      display: flex;
      width: 100%;
      margin-left: 0.6rem;
      position: relative; }
      .employees__search label::before {
        content: "";
        display: block;
        width: 12px;
        height: 12px;
        background-image: url("../img/sprite/icon-search.svg");
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        top: 0.45rem;
        left: -0.5rem; }
    .employees__search input {
      color: #999999;
      background: none;
      border: none;
      width: 100%;
      padding: 0.25rem 0.5rem;
      outline: none; }

